@font-face {
    font-family: Pixelated;
    src: url('../../fonts/pixelated.ttf');
}

.TitleText {
    font-family: Pixelated;
    font-size: 100px;
    padding-left: 70px;
    padding-right: 170px;
    padding-bottom: 10px;
}

.TitleTextSub {
    font-family: Pixelated;
    font-size: 50px;
    padding-left: 70px;
    padding-bottom: 5px;
}

.Links {
    height: 370px;
    width: 40px;
    background-size: cover;
    background-position: center;
}

.LinkTexts {
    list-style-type: none;
    text-decoration: none;
}

.LinkLine {
    display: flex;
    align-items: center;
    justify-content: end;
}

.LinkLineElement {
    display: inline-block;
}

.LinkTexts li {
    font-family: Pixelated;
    font-size: 40px;
    padding-left: 70px;
    padding-right: 10px;
    text-align: right;
}

.LinkTexts a {
    text-decoration: none;
}

.ResumeTitle {
    font-family: Pixelated;
    font-size: 100px;
    padding-top: 100px;
    padding-bottom: 50px;
}

.ResumeHeader {
    font-family: Pixelated;
    font-size: 45px;
    text-align: left;
    color: #f2ac29;
}
.ResumeLeft {
    font-family: Pixelated;
    font-size: 30px;
    text-align: left;
    color: #f7e6b2;
}

.ResumeRight {
    font-family: Pixelated;
    font-size: 35px;
    text-align: right;
    color: #f7e6b2;
}
